<template>
  <div class="root">
      <input class="input"  placeholder="请输入主办方全称" type="text" v-model="name">
       
      <div class="btn-bottom" @click="handleSaveAndBack">保存并返回</div>  
  </div>
</template>

<script>
export default {
  components: {
  },
  data() {
    return {
      name:''
    };
  },
  
  methods: {
    /**
     *   新增主办方
     */
    async reqAddHost(name) {
      const data = {
        name
      }
      const res = await this.$request("host.addHost", data);
      console.log("主办方 :>> ", res);
    },
    async handleSaveAndBack() {
        await this.reqAddHost(this.name)
        this.$router.back()
    }
  },
};
</script>

